import React, { useState } from "react";

import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { Contest, News, ProblemSet, Rating, Tutorials } from "../pages";
import { NavBar, UpComing } from "../components";
import { ThemeProvider, createMuiTheme } from "@material-ui/core/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import { Grid, Box } from "@material-ui/core";

// light theme
const lightTheme = createMuiTheme({
  palette: {
    primary: {
      main: "#247BA0",
      light: "#63ccff",
    },
    secondary: {
      main: "#247BA0",
    },
  },
});

// dark theme
const darkTheme = createMuiTheme({
  palette: {
    type: "dark",

    primary: {
      main: "#247BA0",
    },
    secondary: {
      main: "#247BA0",
    },
  },
});

function App() {
  // theme mode to toggle between light and dark
  const [lightMode, setLightMode] = useState(false);

  const handleLightMode = () => {
    setLightMode((prev) => !prev);
  };

  return (
    <ThemeProvider theme={lightMode ? lightTheme : darkTheme}>
      {/* css baseline provides a consistent, and elegant css  */}
      <CssBaseline />
      <Router>
        <Box>
          <NavBar lightMode={lightMode} handleLightMode={handleLightMode} />
          <Box style={{ margin: "100px 25px 10px 50px" }}>
            <Grid container style={{ position: "relative" }}>
              <Grid item xs={12} sm={2} md={9}>
                <Switch>
                  <Route path="/news" exact component={News} />
                  <Route path="/tutorials" exact component={Tutorials} />
                  <Route path="/contest" exact component={Contest} />
                  <Route path="/problemset" exact component={ProblemSet} />
                  <Route path="/rating" exact component={Rating} />
                </Switch>
              </Grid>
              <Grid item xs={12} sm={1} md={3}>
                <Box style={{ marginLeft: "25px" }}>
                  <UpComing />
                  <UpComing />
                </Box>
              </Grid>
              <Grid item xs={12} sm="auto" md={0}></Grid>
            </Grid>
          </Box>
        </Box>
      </Router>
    </ThemeProvider>
  );
}

export default App;
